import React from 'react';
import PropTypes from 'prop-types';

export default class BlocDescription extends React.Component {
  //constructor(props) {
    //super(props);
    //this.state = {};
  //}

  render() {

    if (this.props.description.__html) {

      this.props.description.__html = this.props.description.__html
        .replace(/\r\n/g, '<br />')
        .replace(/[\r\n]/g, '<br />');

      return(
        <p className={`bloc bloc-description ${this.props.classes}`}
           dangerouslySetInnerHTML={this.props.description}
           style={{ padding: this.props.padding ? '20px' : '10px' }}>
        </p>
      )

    } else {

      return(
        <p className={`bloc bloc-description ${this.props.classes}`}
            style={{ padding: this.props.padding ? '20px' : '10px' }}>
          {this.props.description}
        </p>
      )

    }

  }
}

BlocDescription.propTypes = {
  description: PropTypes.string.isRequired,
  classes: PropTypes.string,
  padding: PropTypes.bool,
};



